@use "../abstracts/index" as *;
@use "../breakpoints/breakpoints" as *;
@use "../components/mixins";

.about-wrapper {
  padding-top: 1.7em;
  p {
    // @include mixins.flexCenter;
    padding: 1em;
    width: 75%;
    margin: auto;
    font-size: 1.7em;
  }
  @media only screen and (min-width: 440px) {
    p {
      font-size: 2em;
    }
  }
  @media only screen and (min-width: 640px) {
    p {
      font-size: 2.4em;
    }
  }
}
