@use "abstracts" as *;

.bg-image {
  width: 99%;
  padding-top: 56.25%;
  margin: 1% auto 0;
  background-size: cover;
  position: relative;
}
.bg-image img {
  position: absolute; /* 2 */
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.card-contents {
  position: absolute; /* 2 */
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  display: flex;
  align-items: center;
  background: $river-bed;
  transition: 0.2s ease-in;
}
.card-contents:hover {
  opacity: 1;
}
.card-texts {
  height: auto;
  padding: 2.5em;
  color: $white-smoke;
  font-family: "Montserrat", sans-serif;
}
.card-title {
  position: relative;
  font-size: 1.1em;
  padding: 2%;
  display: table;
  // background-image: linear-gradient(45deg, #e21738, #ff521b);
  // transform: rotate(0.04rad);

  animation: shake 25s ease-in infinite;
}

.card-content {
  font-size: 1em;

  padding: 2%;
  margin-top: -0.2em;
  display: table;
  // background-image: linear-gradient(45deg, #6303b1, #ff0099);
  // transform: rotate(-0.03rad);
  z-index: 2;
  animation: shake 52s ease-in infinite;
}
