@use "abstracts" as *;
@use "components/mixins";
@use "layouts";
@use "components/cards";
@use "breakpoints/breakpoints" as *;

* {
  padding: 0px;
  margin: 0px;
}
body {
  font-family: $noto;
  background: $dark-shark;
  color: whitesmoke;
  box-sizing: border-box;
  height: 100vh;
}
.hide {
  animation: fadein 5s;
}

@keyframes fadein {
  0% {
    opacity: 0;
  }
  20% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

