@use "abstracts" as *;
@use "components/mixins";

footer {
  position: relative;
  margin-top: -30vh;
  height: 130vh;
  background: hsl(198, 40%, 22%);
  clip-path: polygon(0 10%, 100% 0, 100% 100%, 0% 100%);
  z-index: -1;
}
// .triangle-container {
//   background-color: aqua;
//   clip-path: polygon(0 33%, 100% 0, 100% 100%, 0% 100%);
//   z-index: -1;
// }
.container {
  padding-top: 30vh;
  height: 100vh;
  @include mixins.flexCenter();
  gap: 1.5em;
}

.item {
  img {
    width: 2.5em;
    padding: 1em;
    transition: 0.4s;
  }
}

@media only screen and (min-width: 640px) {
  .container {
    gap: 2em;
  }
  .item {
    img {
      width: 4em;
      padding: 2em;
    }
  }
}
@media only screen and (min-width: 940px) {
  .container {
    gap: 5em;
  }
  .item {
    img {
      width: 5em;
      padding: 3em;
    }
  }
}
.email {
  &:hover {
    background-color: #ce5959;
  }
}
.twitter {
  &:hover {
    background-color: #1c9dec;
  }
}
.facebook {
  &:hover {
    background-color: #0072b1;
  }
}
.github {
  &:hover {
    background-color: #666666;
  }
}
