.projects {
  @include mixins.flexCenter;
}

.flex {
  @include mixins.flexCenter;
  padding-top: 2em;
  padding-bottom: 2em;
  display: flex;
  flex-wrap: wrap;
  width: 98%;
  margin: 0 auto;
}

.card-container {
  display: block;
  flex-basis: 100%;
  max-width: 720px;
}

@media only screen and (min-width: 640px) {
  .card-container {
    flex-basis: 50%;
  }
}
@media only screen and (min-width: 960px) {
  .card-container {
    flex-basis: 33%;
  }
}
